import React, { useState } from 'react';
import '../CSS/Signup.css';
import axios from "axios";
import {useNavigate} from "react-router-dom";

function Signup() {
  const [password, setPassword] = useState('');
  const [password2, setPassword2] = useState('');
  const [businessNumber, setBusinessNumber] = useState('');
  const [companyName, setCompanyName] = useState('');
  const [companyAddress, setCompanyAddress] = useState('');
  const [managerName, setManagerName] = useState('');
  const [managerContact, setManagerContact] = useState('');
  const [email, setEmail] = useState('');
  const [department, setDepartment] = useState('');
  const [position, setPosition] = useState('');

  const [error, setError] = useState('');
  const navigate = useNavigate();

  const navigateToLogin = () => {
    navigate("/");
  };
  const handRegister = async (e) => {
    e.preventDefault(); //     
    setError(''); //    
    console.log(' :', "email :",email,"\n",
        "email : " ,email,
        "phone_number : " ,managerContact,
        "password : " ,password,
        "password2 : " ,password2,
        "company_name : " ,companyName,
        "company_address : " ,companyAddress,
        "business_registration_number : " ,businessNumber,
        "department : " , department,
        "position : " ,position,
        "name : " , managerName
    )
    try {
      const response = await axios.post('https://hizenberk.pythonanywhere.com/api/admin/register/', {
        "email":email,
        "phone_number": managerContact,
        "password": password,
        "password2": password2,
        "company_name": companyName,
        "company_address": companyAddress,
        "business_registration_number": businessNumber,
        "department": department,
        "position": position,
        "name": managerName
      });
      //     (:  ,  )
      console.log(' :', response.data);
      navigateToLogin();
    } catch (err) {
      console.error(' :', err.response?.data);
      setError(' .   .');
    }
  };



  return (
    <div className="signup-container">
      <form className="signup-form">
        <div className="form-group">
          <input type="password"
                 className="input-field"
                 id="password"
                 name="password"
                 placeholder=""
                 onChange={(e) => setPassword(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="password"
                 className="input-field"
                 id="password"
                 name="password"
                 placeholder=" "
                 onChange={(e) => setPassword2(e.target.value)}
          />
          <p className="password-info">,  6 </p>
        </div>
        <div className="form-group">
          <label className="company-info-label"></label>
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="businessNumber"
                 name="businessNumber"
                 placeholder="" onChange={(e) => setBusinessNumber(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="companyName"
                 name="companyName"
                 placeholder=""
                 onChange={(e) => setCompanyName(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="companyAddress"
                 name="companyAddress"
                 placeholder=""
                 onChange={(e) => setCompanyAddress(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="managerName"
                 name="managerName"
                 placeholder=""
                 onChange={(e) => setManagerName(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="position"
                 name="position"
                 placeholder=" "
                 onChange={(e) => setPosition(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="department"
                 name="department"
                 placeholder=" "
                 onChange={(e) => setManagerContact(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="managerDepartment"
                 name="managerDepartment"
                 placeholder="  "
                 onChange={(e) => setDepartment(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="email"
                 className="input-field"
                 id="email"
                 name="email"
                 placeholder=""
                 onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <button type="submit" className="signup-button" onClick={handRegister}></button>
        <p className="terms">
            , <span className="blue-text"> </span>, <span className="blue-text"> </span>, <span
            className="blue-text">   </span>   .
        </p>
      </form>
    </div>
  );
}

export default Signup;
