import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom'; // React Router useNavigate import
import axios from 'axios'; //    axios import
import '../CSS/Sidebar.css';
import {useAuth} from "../Component/AuthContext"; // CSS  import

const Sidebar = () => {
    const [companyName, setCompanyName] = useState(''); //  
    const navigate = useNavigate(); //    useNavigate  
    const location = useLocation(); //   
    const { authState, logout, LogoutSuccess, setLogoutSuccess } = useAuth();
    //    ( )
    // useEffect(() => {
    //     const fetchCompanyName = async () => {
    //         try {
    //             // const token = localStorage.getItem('token'); // localStorage  
    //             // if (token) {
    //                 const response = await axios.get('https://hizenberk.github.io/api/users/');
    //                 console.log(response.data)
    //                 setCompanyName(response.data.company_name); //  
    //             // }
    //         } catch (error) {
    //             console.error('  :', error);
    //         }
    //     };
    //
    //     fetchCompanyName().then(r => null); //     
    // }, []);

    //  :      
    const handleClick = (path) => {
        if (path) {
            navigate(path); //      
        }
    };

    //      
    const menuItems = [
        { name: '', path: '/admin' },
        { name: '', path: '/notice' },
        { name: ' ', path: '/usermanagement' },
        { name: ' ', path: '/CarManagement' }, //   
        { name: '  ', path: '/CarHistory' },
        { name: '', path: '/expensemanagement' },
    ];

    const settingsMenuItems = [
        { name: '', path: '/settings' } //   
    ];

    //       
    const isSelected = (path) => location.pathname.startsWith(path);

    return (
        <div className="sidebar">
            <div className="company-section">
                <div className="profile-circle"></div>
                <div className="company-name">{authState.company_name}</div> {/*   */}
            </div>
            <div className="menu">
                {menuItems.map((item, index) => (
                    <div key={index}>
                        <div
                            className={`menu-item ${isSelected(item.path) ? 'selected' : ''}`} //   'selected'  
                            style={{
                                backgroundColor: isSelected(item.path) ? '#ddd' : '#f8f8f8', //   #ddd,   #f8f8f8
                                fontWeight: isSelected(item.path) ? 'bold' : 'normal' //    bold
                            }}
                            onClick={() => handleClick(item.path)} //     
                        >
                            {item.name} {/*    */}
                        </div>
                    </div>
                ))}
            </div>
            <div className="settings-menu">
                {settingsMenuItems.map((item, index) => (
                    <div key={index}>
                        <div
                            className={`menu-item ${isSelected(item.path) ? 'selected' : ''}`} //   'selected'  
                            style={{
                                backgroundColor: isSelected(item.path) ? '#ddd' : '#f8f8f8', //   #ddd,   #f8f8f8
                                fontWeight: isSelected(item.path) ? 'bold' : 'normal' //    bold
                            }}
                            onClick={() => handleClick(item.path)} //     
                        >
                            {item.name} {/*     */}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default Sidebar;
