import React, { useState } from 'react';
import '../CSS/Signup.css';
import axios from "axios";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../Component/AuthContext"; //      .

function Signup() {
  const { authState, setAuthState } = useAuth(); // authState setAuthState 
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [phonenumber, setPhonenumber] = useState('');
  const [department, setDepartment] = useState('');
  const [position, setPosition] = useState('');
  const [userName, setUserName] = useState('');
  const [error, setError] = useState('');
  const navigate = useNavigate();

  //   
  const navigateToLogin = () => {
    navigate("/usermanagement");
  };

  //    
  const tokenHasExpired = (token) => {
    const decodedToken = JSON.parse(atob(token.split('.')[1]));
    const currentTime = Date.now() / 1000;
    return decodedToken.exp < currentTime; //      true 
  };

  //   
  const refreshAccessToken = async () => {
    try {
      const response = await axios.post('https://hizenberk.pythonanywhere.com/api/token/refresh/', {
        refresh: authState.refresh
      });
      //  access token authState 
      setAuthState((prevState) => ({
        ...prevState,
        access: response.data.access
      }));
    } catch (error) {
      console.error('  ', error);
      //    ,   
      navigate('/login');
    }
  };

  //   
  const handRegister = async (e) => {
    e.preventDefault();
    setError('');

    //    
    if (tokenHasExpired(authState.access)) {
      await refreshAccessToken();
    }

    try {
      const response = await axios.post('https://hizenberk.pythonanywhere.com/api/admin/register-user/', {
        email: email,
        phone_number: phonenumber,
        password: password,
        password2: password,
        department: department,
        position: position,
        name: userName
      }, {
        headers: {
          Authorization: `Bearer ${authState.access}` //   
        }
      });
      console.log(' :', response.data);
      navigateToLogin();
    } catch (err) {
      console.error(' :', err.response?.data);
      setError(' .   .');
    }
  };

  return (
    <div className="signup-container">
      <form className="signup-form">
        <div className="form-group">
          <input type="email"
                 className="input-field"
                 id="email"
                 name="email"
                 placeholder=""
                 onChange={(e) => setEmail(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="password"
                 className="input-field"
                 id="password"
                 name="password"
                 placeholder=""
                 onChange={(e) => setPassword(e.target.value)}
          />
          <p className="password-info">,  6 </p>
        </div>
        <div className="form-group">
          <label className="company-info-label"></label>
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="managerName"
                 name="managerName"
                 placeholder=""
                 onChange={(e) => setUserName(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="phonenumber"
                 className="input-field"
                 id="phonenumber"
                 name="phonenumber"
                 placeholder=""
                 onChange={(e) => setPhonenumber(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="department"
                 name="department"
                 placeholder="  "
                 onChange={(e) => setDepartment(e.target.value)}
          />
        </div>
        <div className="form-group">
          <input type="text"
                 className="input-field"
                 id="position"
                 name="position"
                 placeholder=" "
                 onChange={(e) => setPosition(e.target.value)}
          />
        </div>
        
        <button type="submit" className="signup-button" onClick={handRegister}></button>
        <p className="terms">
            , <span className="blue-text"> </span>, <span className="blue-text"> </span>, <span
            className="blue-text">   </span>   .
        </p>
      </form>
    </div>
  );
}

export default Signup;
